import * as _ from 'lodash';

export type NeuronInfoType = Object & {
  value: number;
  weights: Array<number>;
};

export interface INeuron {
    inputs: Array<number>;
    weights: Array<number>;
    predict: (w:Array<number>) => number;
    activation: () => number;
    info: () => Object & NeuronInfoType;
    // generateWeights: () => void;
}

export class Neuron implements INeuron {
    private _inputs: Array<number>;
    private _weights: Array<number>;

    constructor(inputs: Array<number>) {
        this._inputs = inputs;
    }

    get inputs(): Array<number> {
        return _.clone(this._inputs);
    }

    get weights(): Array<number> {
        return _.clone(this._weights);
    }

    predict(weights?: Array<number>): number {
        if (weights) {
            this._weights = weights;
        }
        let sum = this._inputs.reduce((sum, current, i) => {
            return sum + current * this._weights[i];
        }, 0);
        return sum;
    }

    activation(): number {
        return 1 / (1 + Math.exp(-this.predict()));
    }

    info(): NeuronInfoType {
        return {
            value: this.activation(),
            weights: this.weights
        };
    }

    private generateWeights(): void {
        this._weights = [];
        this.inputs.forEach(() => this._weights.push(Math.random() - 0.5));
    }
}
