import {Layer, LayerInfoType} from "./layer";
import * as _ from 'lodash';


export type NetInfoType = Object & {
    depth: number;
    layers: Array<LayerInfoType>;
};

export type INetOptions = Array<number>;

export interface INet {
    layers: Array<Layer>;
    info: () => NetInfoType;
}

export class Net implements INet {
    private _layers: Array<Layer>;

    constructor(options: INetOptions) {
        this._layers = options.map((depth, i) => new Layer(i, depth));
        console.log(`creating last layer with ${options.length} index`);
        this._layers.push(new Layer(options.length, 1));
        console.log(`created Net with #${this._layers.length} (1 additional)`);
    }

    get layers() {
        return _.clone(this._layers);
    }

    info() {
        return {
            depth: this._layers.length,
            layers: this._layers.map(l => l.info()),
        };
    }

    calculate() {
        this._layers.map(layer    => {
            return layer.vector();
        });
    }
}

export default Net;