import {Neuron, NeuronInfoType} from "./neuron";

export type LayerInfoType = Object & {
    depth: number;
    neurons: Array<NeuronInfoType>;
};

export interface ILayer {
    index: number;
    neurons: Array<Neuron>;
    info: () => LayerInfoType;
}

export class Layer implements ILayer {
    private _index: number;
    private _depth: number;
    private _neurons: Array<Neuron>;

    constructor(index: number, depth: number) {
        this._index = index;
        this._depth = depth;
        console.log(`created Layer #${this._index}, with ${this._depth} neurons allowed`);
    }

    get index() {
        return this._index;
    }

    get neurons() {
        return this._neurons;
    }

    apply(inputs) {
        for (let i = 0; i < this._depth; i++) {
            this._neurons.push(new Neuron(inputs));
        }
    }

    vector() {
        return this._neurons.map(neuron => {
           return neuron.activation();
        });
    }

    info() {
        return {
            depth: this._depth,
            neurons: this._neurons.map(n => n.info())
        };
    }
}